import React from 'react';
import PropTypes from 'prop-types';

function Tempo(props){

  function handleTempoChange(event){
    props.onTempoSelection(parseInt(event.target.value, 10));
  }
  
  return(
    <div className='tempo'>
      <form>
        <label> BPM
          <input type='number' step='1' min='60' max='240' id='tempo' required value={props.selectedTempo} onChange={handleTempoChange}></input>
        </label>
      </form>
    </div>
  );
}

Tempo.propTypes = {
  onTempoSelection: PropTypes.func,
  selectedTempo: PropTypes.number,
};

export default Tempo;
