import React from 'react';
import PropTypes from 'prop-types';

function ClockOutOffset(props){

  function handleClockOutOffsetChange(event){
      props.onClockSubmit(parseInt(event.target.value, 10));
  }

  return(
    <div className="clockout-offset">
      <form>
        <label>Offset</label>
        <input
          type='range'
          min='-32'
          max='0'
          step='1'
          id='clock'
          value={props.offset}
          onChange={handleClockOutOffsetChange}/>
        <p>{props.offset} ms</p>
      </form>
    </div>
  );
}

ClockOutOffset.propTypes = {
  onClockSubmit: PropTypes.func,
  offset: PropTypes.number
};

export default ClockOutOffset;
