import React from 'react';
import PropTypes from 'prop-types';
import './importexport.css';

class ImportExport extends React.Component{
  constructor(props){
    super(props);
    this.handleExport = this.handleExport.bind(this);
    this.handleFileInput = this.handleFileInput.bind(this);

  }

  handleExport(){
    this.props.onExport();
  }

  handleImport(){
    this.props.onImport();
  }

  handleFileInput(event){
    this.props.onFileChange(event);
  }

  render(){
    return(
      <div className="import-export-container">
        <div className="project-name">{this.props.name}</div>
        <button className="impbtn"  onClick={() => this.handleImport()}>Import Project</button>
        <input hidden type="file" id="fileElem"  accept=".json" onChange={() => this.handleFileInput()}/>
        <button className="expbtn"  onClick={() => this.handleExport()}>Export Project</button>
        <div className="logo">qwertySeq()</div>
      </div>
    );
  }
}

ImportExport.propTypes = {
  onExport: PropTypes.func,
  onImport: PropTypes.func,
  onFileChange: PropTypes.func,
  name: PropTypes.string
};

export default ImportExport;
