//sequence related functions
  handlePVChange(sequenceId, updown){
    let newMasterSequenceList = this.state.masterSequenceList.slice();
    let newPlayingSequence = this.state.playingSequence.slice();
    let length = newMasterSequenceList[this.state.selectedBank][sequenceId].sequenceLength;
    let pitchValueArray=[];
    let mode = newMasterSequenceList[this.state.selectedBank][sequenceId].mode;
    if(mode === 1){
      for (let i = 0; i <length; i++) {
        pitchValueArray.push(newMasterSequenceList[this.state.selectedBank][sequenceId].stepSequence[i].value)
      }
    } else {
      for (let i = 0; i <length; i++) {
        pitchValueArray.push(newMasterSequenceList[this.state.selectedBank][sequenceId].stepSequence[i].note)
      }
    }
    if(updown === 'up'){
      if(pitchValueArray.includes(127)){
        return;
      } else if(mode === 1) {
        for (let i = 0; i <length; i++) {
          newMasterSequenceList[this.state.selectedBank][sequenceId].stepSequence[i].value += 1;
          newPlayingSequence[sequenceId].stepSequence[i].value += 1;
        }
      } else if (mode === 0){
        for (let i = 0; i <length; i++) {
          newMasterSequenceList[this.state.selectedBank][sequenceId].stepSequence[i].note += 1;
          newPlayingSequence[sequenceId].stepSequence[i].note += 1;
        }
      }
    } else if(updown === 'down'){
      if(pitchValueArray.includes(0)){
        return;
      } else if(mode === 1) {
        for (let i = 0; i <length; i++) {
          newMasterSequenceList[this.state.selectedBank][sequenceId].stepSequence[i].value -= 1;
          newPlayingSequence[sequenceId].stepSequence[i].value -= 1;
        }
      } else if (mode === 0){
        for (let i = 0; i <length; i++) {
          newMasterSequenceList[this.state.selectedBank][sequenceId].stepSequence[i].note -= 1;
          newPlayingSequence[sequenceId].stepSequence[i].note -= 1;
        }
      }
    }
    this.setState({masterSequenceList: newMasterSequenceList});
    this.setState({playingSequence: newPlayingSequence});
  }

