import React from 'react';
import PropTypes from 'prop-types';
import './memoryaction.css';

function MemoryAction(props){

  function handleActionChange(action){
      props.onActionSelection(action);
  }

  let cancelButton;
  if (props.memoryAction === "Read" || props.memoryAction ==="Copy" ) {
    cancelButton =
      <div className="cancel-btn" onClick={() => handleActionChange('Off')}>
        <button>Cancel Action</button>
      </div>
  }
  let readBtn = "action";
  let copyBtn = "action";
  if (props.memoryAction === "Read") {
    readBtn = "action read";
  } else if ( props.memoryAction === "Copy" ) {
    copyBtn = "action copy";
  }

  return(
    <div className="memory-action-container">
      <div className="action" >
        <p>Bank Mode: {props.memoryAction}</p>
      </div>
      <div className={readBtn} onClick={() => handleActionChange('Read')}>

        <button>Read From Bank</button>
      </div>
      <div className={copyBtn} onClick={() => handleActionChange('Copy')}>
        <button>Copy Into Bank</button>
      </div>
      {cancelButton}
    </div>
  );
}

MemoryAction.propTypes = {
  onActionSelection: PropTypes.func,
  memoryAction: PropTypes.string
};

export default MemoryAction;
