import React from 'react';
import PropTypes from 'prop-types';

function ClockOutEnable(props){

  function handleClockOutEnableChange(event){
      props.onClockEnableSubmit(parseInt(event.target.value, 10));
  }

  return(
    <div className="clockout-enable">
      <form>
        <label>Clock Out</label>
        <select value={props.enabled}
          onChange={handleClockOutEnableChange}>
          <option value='1'>On</option>
          <option value='0'>Off</option>
        </select>
      </form>
    </div>
  );
}

ClockOutEnable.propTypes = {
  onClockEnableSubmit: PropTypes.func,
  enabled: PropTypes.number

};

export default ClockOutEnable;
