import React from 'react';
import Sequence from './Sequence';
import PropTypes from 'prop-types';

function SequenceList(props){
  
  return (
    <div>
      {props.masterSequenceList.map((sequence, index) =>
        <Sequence
          sequence={sequence}
          onSequenceSelection={props.onSequenceSelection}
          selectedSequence={props.selectedSequence}
          onPVShift={props.onPVShift}
          key={index}
          sequenceId={index}
        />
      )}
    </div>
  );
}

SequenceList.propTypes ={
  masterSequenceList: PropTypes.array,
  onSequenceSelection: PropTypes.func,
  selectedSequence: PropTypes.number,
};

export default SequenceList;
