import React from 'react';
import Step from './Step';
import PropTypes from 'prop-types';
import './sequencedetail.css';

class SequenceDetail extends React.Component{
  constructor(props) {
    super(props);
    this.handleMuteChange = this.handleMuteChange.bind(this);
    this.handleModeChange = this.handleModeChange.bind(this);
    this.handleCCChange = this.handleCCChange.bind(this);
    this.handleLengthChange = this.handleLengthChange.bind(this);
    this.handleResolutionChange = this.handleResolutionChange.bind(this);
    this.handleChannelChange = this.handleChannelChange.bind(this);
    this.handleDelayChange = this.handleDelayChange.bind(this);
    this.handleRandomChange = this.handleRandomChange.bind(this);
  }

  handleMuteChange(event){
    this.props.onMuteSubmit(this.props.selectedSequenceId, parseInt(event.target.value, 10));
  }

  handleModeChange(event){
    this.props.onModeSubmit(this.props.selectedSequenceId , parseInt(event.target.value, 10));
  }

  handleCCChange(event){
    this.props.onCCSubmit(this.props.selectedSequenceId , parseInt(event.target.value, 10));
  }

  handleLengthChange(event) {
    this.props.onLengthSubmit(this.props.selectedSequenceId , parseInt(event.target.value, 10));
  }

  handleResolutionChange(event){
    this.props.onResolutionSubmit(this.props.selectedSequenceId , parseInt(event.target.value, 10));
  }

  handleChannelChange(event){
    this.props.onChannelSubmit(this.props.selectedSequenceId, parseInt(event.target.value, 10));
  }

  handleDelayChange(event){
    this.props.onDelayChange(this.props.selectedSequenceId, parseInt(event.target.value, 10));
  }

  handleRandomChange(event){
    this.props.onRandomSubmit(this.props.selectedSequenceId , parseInt(event.target.value, 10));
  }

  render(){
    const colorArray = ['pink', 'red', 'orange', 'yellow', 'green', 'teal', 'blue', 'purple'];
    let sequenceTitleClass = 'title ' + colorArray[this.props.selectedSequenceId];
    let ccModeMenu = <div></div>;
    if(this.props.selectedSequenceSlice.mode === 1){
      ccModeMenu =
      <div className='cc'>
        <form>
          <label>CC #
            <select value={this.props.selectedSequenceSlice.cc}
              onChange={this.handleCCChange}>
              <option value='0'>0</option>
              <option value='1'>1</option>
              <option value='2'>2</option>
              <option value='4'>4</option>
              <option value='5'>5</option>
              <option value='6'>6</option>
              <option value='7'>7</option>
              <option value='8'>8</option>
              <option value='10'>10</option>
              <option value='11'>11</option>
              <option value='12'>12</option>
              <option value='13'>13</option>
              <option value='16'>16</option>
              <option value='17'>17</option>
              <option value='18'>18</option>
              <option value='19'>19</option>
              <option value='32'>32</option>
              <option value='33'>33</option>
              <option value='34'>34</option>
              <option value='36'>36</option>
              <option value='37'>37</option>
              <option value='38'>38</option>
              <option value='39'>39</option>
              <option value='40'>40</option>
              <option value='42'>42</option>
              <option value='43'>43</option>
              <option value='44'>44</option>
              <option value='45'>45</option>
              <option value='64'>64</option>
              <option value='65'>65</option>
              <option value='66'>66</option>
              <option value='67'>67</option>
              <option value='68'>68</option>
              <option value='69'>69</option>
              <option value='70'>70</option>
              <option value='71'>71</option>
              <option value='72'>72</option>
              <option value='73'>73</option>
              <option value='74'>74</option>
              <option value='75'>75</option>
              <option value='76'>76</option>
              <option value='77'>77</option>
              <option value='78'>78</option>
              <option value='79'>79</option>
              <option value='80'>80</option>
              <option value='81'>81</option>
              <option value='82'>82</option>
              <option value='83'>83</option>
              <option value='91'>91</option>
              <option value='92'>92</option>
              <option value='93'>93</option>
              <option value='94'>94</option>
              <option value='95'>95</option>
              <option value='96'>96</option>
              <option value='97'>97</option>
              <option value='98'>98</option>
              <option value='99'>99</option>
              <option value='100'>100</option>
              <option value='101'>101</option>
            </select>
          </label>
        </form>
      </div>
    }

    return (
      <div className='sequence-detail-container'>
        <div className='header'>
          <div className='sequence-name-grid'>
            <h1 className='title'>Bank {this.props.selectedBankId + 1}</h1>
            <h1 className={sequenceTitleClass}>Sequence {this.props.selectedSequenceId + 1}</h1>
          </div>
          <div className='sequence-parameters-grid'>
            <div className='mute'>
              <form>
                <label>On/Off</label>
                <select value={this.props.selectedSequenceSlice.isMuted}
                  onChange={this.handleMuteChange}>
                  <option value='1'>Off</option>
                  <option value='0'>On</option>
                </select>
              </form>
            </div>
            <div className='mode'>
              <form>
                <label>Mode
                  <select id='menu1' value={this.props.selectedSequenceSlice.mode}
                    onChange={this.handleModeChange}>
                    <option value='0'>Notes</option>
                    <option value='1'>CC</option>
                  </select>
                </label>
              </form>
            </div>
            <div className='sequence-length'>
              <form>
                <label>Length</label>
                <select id='menu2' value={this.props.selectedSequenceSlice.sequenceLength} onChange={this.handleLengthChange}>
                  <option value='1'>1</option>
                  <option value='2'>2</option>
                  <option value='3'>3</option>
                  <option value='4'>4</option>
                  <option value='5'>5</option>
                  <option value='6'>6</option>
                  <option value='7'>7</option>
                  <option value='8'>8</option>
                  <option value='9'>9</option>
                  <option value='10'>10</option>
                  <option value='11'>11</option>
                  <option value='12'>12</option>
                  <option value='13'>13</option>
                  <option value='14'>14</option>
                  <option value='15'>15</option>
                  <option value='16'>16</option>
                </select>
              </form>
            </div>
            <div className='resolution'>
              <form>
                <label>Resolution</label>
                <select id='menu3' value={this.props.selectedSequenceSlice.sequenceResolution }
                  onChange={this.handleResolutionChange}>
                  <option value='1'>4</option>
                  <option value='2'>8</option>
                  <option value='4'>16</option>
                  <option value='8'>32</option>
                </select>
              </form>
            </div>
            <div className='output-channel'>
              <form>
                <label>MIDI Channel</label>
                <select value={this.props.selectedSequenceSlice.sequenceChannelOutput} onChange={this.handleChannelChange}>
                  <option value='1'>1</option>
                  <option value='2'>2</option>
                  <option value='3'>3</option>
                  <option value='4'>4</option>
                  <option value='5'>5</option>
                  <option value='6'>6</option>
                  <option value='7'>7</option>
                  <option value='8'>8</option>
                  <option value='9'>9</option>
                  <option value='10'>10</option>
                  <option value='11'>11</option>
                  <option value='12'>12</option>
                  <option value='13'>13</option>
                  <option value='14'>14</option>
                  <option value='15'>15</option>
                  <option value='16'>16</option>
                </select>
              </form>
            </div>
            {ccModeMenu}
            <div className='delay'>
              <form>
                <label>Delay</label>
                <input
                  type='range'
                  min='0'
                  max='32'
                  step='1'
                  id='delay'
                  value={this.props.selectedSequenceSlice.delay}
                  onChange={this.handleDelayChange}/>
                <p>{this.props.selectedSequenceSlice.delay} ms</p>
              </form>
            </div>
            <div className='random'>
              <form>
                <label>Random
                  <select value={this.props.selectedSequenceSlice.random}
                    onChange={this.handleRandomChange}>
                    <option value='0'>Off</option>
                    <option value='1'>Ceiling</option>
                    <option value='2'>Floor</option>
                  </select>
                </label>
              </form>
            </div>
          </div>
        </div>
        <div>
          <ul className='step-flow-grid'>
            {this.props.selectedSequence.map((step, index) => {
              let stepContainerClass;
              if (index < this.props.selectedSequenceSlice.sequenceLength) {
                stepContainerClass = '';
              } else {
                stepContainerClass = 'hide';
              }
              return <li className={stepContainerClass}>
                <Step
                  length={this.props.selectedSequenceSlice.sequenceLength}
                  mode={this.props.selectedSequenceSlice.mode}
                  selectedSequence={this.props.selectedSequence}
                  selectedSequenceId={this.props.selectedSequenceId}
                  onStepSelection={this.props.onStepSelection}
                  selectedStepId={this.props.selectedStepId}
                  onEnableChange={this.props.onEnableChange}
                  onValueChange={this.props.onValueChange}
                  onPitchChange={this.props.onPitchChange}
                  onVelocityChange={this.props.onVelocityChange}
                  onGateChange={this.props.onGateChange}
                  onOffsetChange={this.props.onOffsetChange}
                  onTieChange={this.props.onTieChange}
                  onStepJam={this.props.onStepJam}
                  note={step.note}
                  velocity={step.velocity}
                  gate={step.gate}
                  offset={step.offset}
                  isOn={step.isOn}
                  tie={step.tie}
                  key={index}
                  stepId={index}
                />
            </li>
            })}
          </ul>
        </div>
      </div>
    );
  }
}
SequenceDetail.propTypes = {
  selectedSequence: PropTypes.array,
  selectedBankId: PropTypes.number,
  selectedSequenceId: PropTypes.number,
  selectedStepId: PropTypes.number,
  onStepSelection: PropTypes.func,
  onMuteSubmit: PropTypes.func,
  onModeSubmit: PropTypes.func,
  onCCSubmit:PropTypes.func,
  onLengthSubmit: PropTypes.func,
  onResolutionSubmit: PropTypes.func,
  onChannelSubmit: PropTypes.func,
  onDelayChange: PropTypes.func,
  onRandomSubmit: PropTypes.func,
  onEnableChange: PropTypes.func,
  onValueChange: PropTypes.func,
  onPitchChange: PropTypes.func,
  onVelocityChange: PropTypes.func,
  onGateChange: PropTypes.func,
  onOffsetChange: PropTypes.func,
  onTieChange: PropTypes.func,
  onStepJam: PropTypes.func,
};
export default SequenceDetail;
