import React from 'react';
import PropTypes from 'prop-types';
import './memorybank.css';

function MemoryBank(props){
  let memoryBankClass;
  if (props.bankId === props.selectedBank) {
    memoryBankClass = 'memory-bank-active';
  } else {
    memoryBankClass = 'memory-bank';
  }

  function handleBankChange(bank, action){
      props.onBankSelection(bank, action);
  }

  let memoryBankReadKeys =['!','@', '#','$', '%','^','&','*'];
  let memoryBankCopyKeys =['Z','X','C','V','B','N','M','<']

  return(
    <div className={memoryBankClass}>
      <div className="bank-item" onClick={() => handleBankChange(props.bankId, props.selectedAction)}>
        <button>Bank&nbsp;<span className="read-shortcut">&nbsp;{memoryBankReadKeys[props.bankId]}&nbsp;</span>&nbsp;:&nbsp;<span className="copy-shortcut">&nbsp;{memoryBankCopyKeys[props.bankId]}&nbsp;</span></button>
      </div>
    </div>
  );
}

MemoryBank.propTypes = {
  onBankSelection: PropTypes.func,
  bankId: PropTypes.number,
  selectedBank: PropTypes.number,
  selectedAction: PropTypes.string
};

export default MemoryBank;
