import React from 'react';
import PropTypes from 'prop-types';

function OutputSelector(props){

  function handleMidiOutputChange(event){
    props.onOutputSubmit(event.target.value);

  }
  return(
    <div className="output-selector">
      <form>
        <label className="output-selector-label">MIDI Device</label>
        <select className="output-selector-select" value={props.selectedMidiOut} onChange={handleMidiOutputChange} >
          {props.allOutputs.map((output, index) =>
            <option value={output._midiOutput.name} key={index}>{output._midiOutput.name}</option>
          )}
        </select>
      </form>
    </div>
  );
}

OutputSelector.propTypes = {
  onOutputSubmit: PropTypes.func,
  selectedMidiOut: PropTypes.string,
  allOutputs: PropTypes.array,
};

export default OutputSelector;
