import React from 'react';
import PropTypes from 'prop-types';

function AuditionEnable(props){

  function handleAuditionSelection(event){
    props.onAuditionSelection(parseInt(event.target.value, 10));
  }

  return(
    <div className='audition-enable'>
      <form>
        <label>Preview
          <select value={props.clickPlayEnabled} onChange={handleAuditionSelection}>
            <option value= '1'>On</option>
            <option value= '0'>Off</option>
          </select>
        </label>
      </form>
    </div>
  );
}

AuditionEnable.propTypes = {
  onAuditionSelection: PropTypes.func,
  clickPlayEnabled: PropTypes.number,
};

export default AuditionEnable;
