import React from 'react';
import PropTypes from 'prop-types';
import './audiorecord.css';

function AudioRecord(props){

  function handleAudioInputChange(event){
    props.onDeviceSelect(event.target.value);
  }

  function handleThruEnable(event){
    props.onThruEnable(parseInt(event.target.value),10)
  }

  function handleRecordEnable(event){
    props.onRecordEnable(parseInt(event.target.value),10)
  }

  function handleTail(event){
    props.onTailSelection(parseInt(event.target.value),10)
  }

  function handleChannels(event){
    props.onChannelCount(parseInt(event.target.value),10);
  }

  function handleRecordingDownload(){
    props.onDownloadClick();
  }

  function handleRecordingDelete(){
    props.onDeleteClick();
  }

  return(
    <div className='audio-record-container' id='audio'>
      <div className="return-input">
        <form>
          <label>Return Input</label>
          <select value={props.selectedDevice} onChange={handleAudioInputChange} >
            {props.inputDevices.map((input, index) =>
              <option value={input.deviceId} key={index}>{input.label}</option>
            )}
          </select>
        </form>
      </div>
      <div className='audio-record-grid'>
        <div className='return-monitor'>
          <form>
            <label>Return Monitor</label>
            <select value={props.thruStatus}
              onChange={handleThruEnable}
              >
              <option value='0'>Disabled</option>
              <option value='1'>Enabled</option>
            </select>
          </form>
        </div>
        <div className='record-enable'>
          <form>
            <label>Record Enable</label>
            <select value={props.enableStatus}
              onChange={handleRecordEnable}
              >
              <option value='0'>No</option>
              <option value='1'>Yes</option>
            </select>
          </form>
        </div>
      </div>
      <div className='audio-record-grid'>
        <div className='mono-stereo'>
          <form>
            <label>Mono/Stereo</label>
            <select value={props.channels}
              onChange={handleChannels}
              >
              <option value='1'>Mono</option>
              <option value='2'>Stereo</option>
            </select>
          </form>
        </div>
        <div className='tail'>
          <form>
            <label>Tail</label>
            <select value={props.tailLength}
              onChange={handleTail}
              >
              <option value='0'>None</option>
              <option value='100'>100ms</option>
              <option value='300'>300ms</option>
              <option value='1000'>1s</option>
              <option value='3000'>3s</option>
              <option value='5000'>5s</option>
              <option value='10000'>10s</option>
            </select>
          </form>
        </div>
      </div>
      <div className='audio-player'>
        <audio controls controlsList='nodownload' src={props.playerSource}/>
      </div>
      <div className='audio-record-grid'>
        <button className="downloadbtn" onClick={() => handleRecordingDownload()} >Download Recording</button>
        <button className="deletebtn" onClick={() => handleRecordingDelete()} >Delete Recording</button>
      </div>
    </div>
  );
}

AudioRecord.propTypes = {
  inputDevices: PropTypes.array,
  selectedDevice: PropTypes.string,
  onThruEnable: PropTypes.func,
  thruStatus: PropTypes.number,
  enableStatus: PropTypes.number,
  tailLength: PropTypes.number,
  onDeviceSelect: PropTypes.func,
  onRecordEnable: PropTypes.func,
  onTailSelection: PropTypes.func,
  onChannelCount: PropTypes.func,
  channels: PropTypes.number,
  playerSource: PropTypes.string,
  onDownloadClick: PropTypes.func,
  onDeleteClick: PropTypes.func,
};

export default AudioRecord;
