import React from 'react';
import'./step.css';
import PropTypes from 'prop-types';

class Step extends React.Component{
  constructor(props) {
    super(props);
    this.handleIsOnChange = this.handleIsOnChange.bind(this);
    this.handleValueChange = this.handleValueChange.bind(this);
    this.handleNoteChange = this.handleNoteChange.bind(this);
    this.handleVelocityChange = this.handleVelocityChange.bind(this);
    this.handleGateChange = this.handleGateChange.bind(this);
    this.handleOffsetChange = this.handleOffsetChange.bind(this);
    this.handleTieChange = this.handleTieChange.bind(this);
  }

  handleIsOnChange(event) {
    this.props.onEnableChange(this.props.selectedSequenceId, this.props.stepId, parseInt(event.target.value, 10));
  }

  handleValueChange(event) {
    this.props.onValueChange(this.props.selectedSequenceId, this.props.stepId, parseInt(event.target.value, 10));
  }

  handleNoteChange(event) {
    this.props.onPitchChange(this.props.selectedSequenceId, this.props.stepId, parseInt(event.target.value, 10));
  }

  handleVelocityChange(event) {
    this.props.onVelocityChange(this.props.selectedSequenceId, this.props.stepId, parseInt(event.target.value, 10));
  }

  handleGateChange(event) {
    this.props.onGateChange(this.props.selectedSequenceId, this.props.stepId, parseInt(event.target.value, 10));
  }

  handleOffsetChange(event) {
    this.props.onOffsetChange(this.props.selectedSequenceId, this.props.stepId, parseInt(event.target.value, 10));
  }

  handleTieChange(event) {
    this.props.onTieChange(this.props.selectedSequenceId, this.props.stepId, parseInt(event.target.value, 10));
  }


  render(){
    const colorArray = ['#ff50bf', '#ff211d', '#ff5518', '#f7d842', '#9bff01', '#2ca8c2', '#185fff', '#b44afe'];
    let sliderColor = colorArray[this.props.selectedSequenceId];
    let isEnabled;
    if(this.props.selectedSequence[this.props.stepId].isOn === 1){
      isEnabled = 'On';
    } else {
      isEnabled = 'Off';
    }
    let isTied;
    if(this.props.selectedSequence[this.props.stepId].tie === 1){
      isTied = 'On';
    } else {
      isTied = 'Off';
    }
    let tieDisplay = <div></div>;
    if(this.props.stepId > 0){
      tieDisplay =
        <div>
          <label>
            <span>Tie</span>
            <span>{isTied}</span>
          </label>
          <input
            type='range'
            min='0'
            max='1'
            step='1'
            id='tie'
            value={this.props.selectedSequence[this.props.stepId].tie}
            onChange={this.handleTieChange}/>
        </div>
    }

    let keyStep = ['q','w','e','r','t','y','u','i','a','s','d','f','g','h','j','k'];
    let capKeyStep = ['Q','W','E','R','T','Y','U','I','A','S','D','F','G','H','J','K'];
    let stepDisplay = null;

    if(this.props.mode === 0 && this.props.stepId < this.props.length){
      stepDisplay =
      <form onClick={() => {this.props.onStepJam(this.props.selectedSequenceId, this.props.stepId);}}>
        <div className='step-label-flex' >
          <div className='step-keyicon'>{keyStep[this.props.stepId]}</div>
          <div>
            <label>
              <span>{isEnabled}</span>
            </label>
            <input
            type='range'
            min='0'
            max='1'
            step='1'
            id='isOn'
            value={this.props.selectedSequence[this.props.stepId].isOn}
            onChange={this.handleIsOnChange}/>
          </div>
        </div>
        <div className='step-label-flex' >
          <div className='step-shifticon'>{capKeyStep[this.props.stepId]}</div>
          <div style={{fontSize: '1rem'}}>,</div>
          <div className='step-tabicon'>tab</div>
        </div>
        <div>
          <label>
            <span>Pitch</span>
            <span>{this.props.selectedSequence[this.props.stepId].note}</span>
          </label>
          <input
            type='range'
            min='0'
            max='127'
            step='1'
            id='note'
            name={this.props.stepId}
            value={this.props.selectedSequence[this.props.stepId].note}
            onChange={this.handleNoteChange}/>
        </div>
        <div>
          <label>
            <span>Velocity</span>
            <span>{this.props.selectedSequence[this.props.stepId].velocity}</span>
          </label>
          <input
            type='range'
            min='0'
            max='127'
            step='1'
            id='velocity'
            value={this.props.selectedSequence[this.props.stepId].velocity}
            onChange={this.handleVelocityChange}/>
        </div>
        <div>
          <label>
            <span>Gate</span>
            <span>{this.props.selectedSequence[this.props.stepId].gate}ms</span>
          </label>
          <input
            type='range'
            min='0'
            max='127'
            step='1'
            id='gate'
            value={this.props.selectedSequence[this.props.stepId].gate}
            onChange={this.handleGateChange}/>
        </div>
        <div>
          <label>
            <span>Offset</span>
            <span>{this.props.selectedSequence[this.props.stepId].offset}ms</span>
          </label>
          <input
            type='range'
            min='0'
            max='127'
            step='1'
            id='offset'
            value= {this.props.selectedSequence[this.props.stepId].offset}
            onChange={this.handleOffsetChange}/>
        </div>
        {tieDisplay}
      </form>
    } else if (this.props.mode === 1 && this.props.stepId < this.props.length){
      stepDisplay =
      <form>
        <div className='step-label-flex'>
          <div className='step-keyicon'>{keyStep[this.props.stepId]}</div>
          <div>
            <label>
              <span>{isEnabled}</span>
            </label>
            <input
            type='range'
            min='0'
            max='1'
            step='1'
            id='isOn'
            value={this.props.selectedSequence[this.props.stepId].isOn}
            onChange={this.handleIsOnChange}/>
          </div>
        </div>
        <div className='step-label-flex' >
          <div className='step-shifticon'>{capKeyStep[this.props.stepId]}</div>
          <div style={{fontSize: '1rem'}}>,</div>
          <div className='step-tabicon'>tab</div>
        </div>
        <div>
          <label>
            <span>Value</span>
            <span>{this.props.selectedSequence[this.props.stepId].value}</span>
          </label>
          <input
            type='range'
            min='0'
            max='127'
            step='1'
            id='note'
            name={this.props.stepId}
            value={this.props.selectedSequence[this.props.stepId].value}
            onChange={this.handleValueChange}/>
        </div>
        <div>
          <label>
            <span>Offset</span>
            <span>{this.props.selectedSequence[this.props.stepId].offset}ms</span>
          </label>
          <input
            type='range'
            min='0'
            max='127'
            step='1'
            id='offset'
            value= {this.props.selectedSequence[this.props.stepId].offset}
            onChange={this.handleOffsetChange}/>
        </div>
      </form>
    }
    return (
      <div>
        {/* Set color for slider */}
        <style jsx>{`
           .step-wrapper input[type=range]::-webkit-slider-thumb{
             background-color: ${sliderColor};
           }
       )`}</style>

          <div className='step-wrapper' onClick={() => {this.props.onStepSelection(this.props.stepId);}}>
            {stepDisplay}
          </div>
      </div>
    );
  }
}

Step.propTypes = {
  selectedSequence: PropTypes.array,
  mode: PropTypes.number,
  length: PropTypes.number,
  selectedSequenceId: PropTypes.number,
  stepId: PropTypes.number,
  onStepSelection: PropTypes.func,
  onEnableChange: PropTypes.func,
  onValueChange: PropTypes.func,
  onPitchChange: PropTypes.func,
  onVelocityChange: PropTypes.func,
  onGateChange: PropTypes.func,
  onOffsetChange: PropTypes.func,
  onTieChange: PropTypes.func,
  onStepJam: PropTypes.func,
};

export default Step;
