import React from 'react';
import PropTypes from 'prop-types';
import './sequence.css';

function Sequence(props){

  function handlePVGlobal(sequenceId, updown){
    props.onPVShift(sequenceId, updown);
  }

  let disableKeys = ['z', 'x', 'c', 'v', 'b', 'n', 'm', ',']
  let mode = '';
  let cc = '';
  let sequenceDisplayClass;
  if(props.sequence.isMuted === 1){
    sequenceDisplayClass = 'sequence transparent';
  } else {
    sequenceDisplayClass = 'sequence';
  }
  if (props.sequence.mode === 0){
    mode = 'NOTE';
  } else {
    mode = 'CC';
    cc = props.sequence.cc;
  }

  return (

    <div className={sequenceDisplayClass} onClick={() => {props.onSequenceSelection(props.sequenceId);}}>
      <div className='sequence-flex'>
        <h1>{props.sequenceId + 1} : {disableKeys[props.sequenceId]}</h1>
        <div className='sequence-step-grid'>
          {props.sequence.stepSequence.map((step, index) => {
            let stepFillClass;
            let stepContainerClass;
            if (index < props.sequence.sequenceLength) {
              stepContainerClass = 'step-container';
              if (step.isOn === 1) {
                stepFillClass = 'step-fill';
              } else {
                stepFillClass = 'step-fill step-off';
              }
            } else {
              stepContainerClass = 'step-container hide';
            }
            if (mode === 'NOTE') {
              let heightNote = Math.round(((step.note / 127)*100));
              return  <div className={stepContainerClass}>
                        <div className={stepFillClass} style={{height: heightNote + '%'}}></div>
                      </div>;
            } else {
              let heightCC = Math.round(((step.value / 127)*100));
              return  <div className={stepContainerClass}>
                        <div className={stepFillClass} style={{height: heightCC + '%'}}></div>
                      </div>;
            }
          })}
        </div>
        <div className='sequence-flex-column'>
          <p>OUT</p>
          <p>{props.sequence.sequenceChannelOutput}</p>
        </div>
        <div className='sequence-flex-column'>
          <p>{mode}</p>
          <p>{cc}</p>
        </div>
        <div className='sequence-flex-column'>
          <div className='sequence-nudge-btn'onClick={() => {handlePVGlobal(props.sequenceId, 'up');}}>P/V +</div>
          <div className='sequence-nudge-btn'onClick={() => {handlePVGlobal(props.sequenceId,'down');}}>P/V -</div>
        </div>
      </div>
    </div>
  );
}

Sequence.propTypes = {
  sequence: PropTypes.object,
  onPVShift: PropTypes.func,
  onSequenceSelection: PropTypes.func,
  sequenceId: PropTypes.number,
};

export default Sequence;
